.clear {
    * {
      box-sizing: border-box;
      outline: none;
    }
  
    a {
      color: inherit;
      text-decoration: none;
    }
  
    ul {
      margin: 0;
      padding: 0;
    }
  
    li {
      list-style-type: none;
    }
    
}
.topList {
  display: flex;
  padding: 0 0 30px;
}
